@import '../variables';

.header {
	border-bottom: 1px solid $gray-line;
	padding: 40px 5px;
	@media (max-width: 645px) {
		padding: 20px 5px;
	}
	.container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@media (max-width: 999px) {
			flex-direction: column;
			gap: 20px;
		}
	}

	&__logo {
		display: flex;
		align-items: center;

		img {
			margin-right: 15px;
		}

		h1 {
			color: #181818;
			font-size: 24px;
			letter-spacing: 1%;
			text-transform: uppercase;
			font-weight: 800;
			@media (max-width: 645px) {
				font-size: 18px;
			}
		}

		p {
			color: #7b7b7b;
			@media (max-width: 645px) {
				display: none;
			}
		}
		@media (max-width: 645px) {
			gap: 30px;
		}
	}
	&__cart {
		@media (max-width: 999px) {
			width: 70%;
			.button--cart {
				justify-content: center;
			}
		}
	}
}
